import React from "react";
import Tilt from "react-tilt";
import alien from "./alien.png";
import "./Logo.css";

const Logo = () => {
  return (
    <div className="ma4 mt0">
      <Tilt className="Tilt br2 shadow-2" options={{ max: 55 }}>
        <div className="Tilt-inner pa3 ">
          <img alt="logo" src={alien} />
        </div>
      </Tilt>
    </div>
  );
};

export default Logo;
